<template>
  <div id="app">
    <h1>{{title}}</h1>
    <input class="addInput" v-model="newItem" @keyup.enter="addNew()" plackholder="输入你的todo清单"/>
    <ul id="list">
      <li v-for="(item,index) in items" :class="{finished:item.isFinished}" @click="todo(item)">
        {{item.label}} <span @click="deleteItem(index,item)">X</span>
      </li>
    </ul>
  </div>
</template>

<script>
  import Store from './store.js';

  export default {
    data:function(){
      return {
        title:'this is a todo list',
        items:Store.fetch()?Store.fetch()
                :[{
                  label:"it is a test",
                  isFinished:"true"
                }],
        newItem:''
      }
    },
    methods:{
      todo:function(item){
        item.isFinished=!item.isFinished;
//      item.isFinished?
      },
      addNew:function(){
        if(!this.newItem){return}
        this.items.push({
          label:this.newItem,
          isFinished:false
        });
        this.newItem='';
      },
      deleteItem:function(index,item){
        this.items.splice(index,1);
        window.event? window.event.cancelBubble = true : e.stopPropagation();
      }
    },
    watch:{
      items:{
        handler:function(items){
          Store.save(items);
        },
        deep:true
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
  /*输入框样式*/
  .addInput{
    width:233px;

  }
  /*todo列表样式*/
  #list{
    padding:0;
  }
  #list li{
    width:233px;
    height:20px;
    margin:10px auto;
    list-style: none;
    border-radius: 2px;
    text-align: left;
  }
  #list li.finished{
    text-decoration: line-through;
    color:darkgrey;
  }
  #list li.finished span{
    text-decoration: line-through;
    color:darkgrey;
  }
  #list li.finished span:hover{
    cursor:pointer;
    text-decoration: none;
    color: #2c3e50;
  }
  #list li span{
    float: right;
  }
</style>


